'use strict';

var React = require('react');
var Reflux = require('reflux');
var EsriService=require('./esriService.jsx');

module.exports  = React.createClass({displayName: "exports",


  componentWillMount:function(){
    console.log("layers->manager: componentWillMount");

  },

  componentWillUnmount:function(){
    console.log("layers->manager: componentWillUnmount");

  },


  render: function() {
    console.log("layers->manager: render");
    var services=this.props.services|| [];
    return (
      React.createElement("div", {className: "layerList"}, 
      React.createElement("h3", null, "Map Layers"), 
      
        services.map(function(s){
          return (React.createElement(EsriService, {onChange: this.props.onChange, service: s}))
        }.bind(this))
      
      )
      );

  }

});