var React = require('react');
var Reflux = require('reflux');
var EsriLayer=require('./esriLayer.jsx');

module.exports =React.createClass({displayName: "exports",



	getDefaultProps: function() {
       return {expanded:false};
  	},

	toggle:function(){
		this.props.expanded=( !this.props.expanded);
	  	this.forceUpdate();
	},

	handleVisbility:function(){
		this.props.service.defaultVisibility=(!this.props.service.defaultVisibility);
		this.updateAll(this.props.service.defaultVisibility);

	},

   /*tell parrent something has changed */
	update:function(){
		this.props.onChange();

	},

	updateAll:function(isVisible){
		this.props.service.layers.map( function (l){
			l.defaultVisibility=isVisible;
		});
		this.forceUpdate();
		this.update();
	},

	render: function() {
		var service=this.props.service;

		var layers=this.props.service.layers;
		return (
				React.createElement("div", null, 
					React.createElement("ul", null, 
						React.createElement("li", null, 
							React.createElement("span", {onClick: this.toggle, className: "clickeable"}, 
								service.serviceDescription
							), 
							
							React.createElement("span", null, 
								" ", React.createElement("i", {onClick: this.toggle, className: this.props.expanded?"fa small fa-minus":"fa small fa-plus"})
							), 
							
							React.createElement("ul", {ref: "list", className: this.props.expanded?"expanded":"collapsed"}, 
								
									layers.map( function (l){
										return (React.createElement(EsriLayer, {onChange: this.update, layer: l}))
									}.bind(this))
							
							)
						)
					)
				)
				)
	}

});
